button.ds-btn {
    /* remove browser styling from button element */
    -webkit-appearance: none;
    -moz-appearance: none;
}

/* primary button */
.ds-btn,
.ds-btn-primary {
    border: 0;
    box-sizing: border-box;
    border-radius: 2px;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    display: inline-block;
    padding: 12px 18px;
    margin: 4px 0;
    text-decoration: none;
    color: white;
    background-color: $color-1;
    border: 2px solid transparent;
    transition: background-color 0.05s ease-in-out;
    cursor: pointer;

    &:visited {
        color: white;
    }

    &.ds-btn--disabled {
        opacity: 0.25;
        pointer-events: none;
    }

    &:hover {
        text-decoration: underline;
        background-color: darken($color-1,5%);
    }

    &[class*="icon-"]{
        padding-right: 44px;
        &::after {
            filter: $white;
            margin: 0 12px;
        }
    }

    &.ds-icon--before {
        padding-right: 18px;
        padding-left: 44px;
        &::after {
            left: 0;
            right: unset;
        }
    }

    @media (min-width: 1400px) {
        font-size: 18px;
        line-height: 28px;
    }
}

/* primary button modifiers */
.ds-btn--light,
.ds-btn-primary--light {
    color: $color-1;
    background-color: white;

    &:visited {
        color: $color-1;
    }

    &:hover {
        text-decoration: underline;
        background-color: darken(white,5%);
    }

    &[class*="icon-"]{
        &::after {
            filter: $red;
        }
    }
}

/* secondary button */
.ds-btn-secondary {
    @extend .ds-btn;
    color: $color-2;
    background-color: transparent;
    border: 2px solid $color-2;
    padding: 12px 18px;

    &:visited {
        color: $color-2;
    }

    &:hover {
        background-color: rgba(black,.15);
        border: 2px solid $color-2;
    }

    &[class*="icon-"]::after {
        filter: $near-black;
    }
}

/* secondary button modifiers*/
.ds-btn-secondary--light {
    color: white;
    border: 2px solid white;

    &:visited {
        color: white;
    }

    &:hover {
        background-color: rgba(black,.15);
        border: 2px solid white;
    }

    &[class*="icon-"]::after {
        filter: $white;
    }
}

/* link button - looks like a link (for button tag) */
.ds-btn-link {
    padding: 0;
    margin: 0;
    color: $color-1;
    background: none;
    text-decoration: underline;
    border: 0;
    border-radius: 0px;

    &:hover {
        background: none;
        text-decoration: none;
    }
}

/* common button modifiers*/
.ds-btn--small,
.ds-btn-secondary--small {
    padding: 4px 16px;

    &[class*="icon-"]{
        padding-right: 36px;
        &::after {
            margin: 0 8px;
            width: 16px;
        }
    }

    &.ds-icon--before {
        padding-right: 16px;
        padding-left: 28px;

        &::after {
            left: 0;
            right: unset;
        }
    }
}

/* pill button */
.ds-btn-pill {
    @extend .ds-btn-secondary;
    font-weight: 500;
    border: 2px solid $color-2;
    box-sizing: border-box;
    font-size: 16px;
    line-height: inherit;
    display: inline-block;
    padding: 4px 16px;
    border-radius: 20px;
    color: $color-2;
    text-decoration: none;
    background-color: white;
    cursor: pointer;

    &:visited {
        color: $color-2;
    }

    &:hover {
        text-decoration: underline;
        background-color: rgba(black,.15);
    }
}

/* pill button modifiers */
.ds-btn-pill--selected {
    color: white;
    background-color: $color-2;
}

/* round button */
.ds-btn-round {
    @extend .ds-btn-pill;
    background-color: transparent;
    width: 48px;
    height: 48px;
    padding: 0;
    text-align: center;
    border-radius: 50%;
    text-indent: 100%;
    overflow: hidden;
    white-space: nowrap;
    &[class*="icon-"] {
        padding: 0;
    }
    &[class*="icon-"]::after {
        position: absolute;
        right: 0;
        top: 0;
        bottom: 0;
        left: 0;
        margin: auto;
        transform: none;
    }
}

/* round button modifiers */
.ds-btn-round--small {
    width: 36px;
    height: 36px;
}

.ds-btn-round--light {
    color: white;
    border: 2px solid white;
    &:visited {
        color: white;
    }
    &:hover {
        background-color: rgba(black,.15);
        border: 2px solid white;
    }
    &[class*="icon-"]::after {
        filter: $white;
    }
}


/* form button */
input[type="button"] {
    @extend .ds-btn;
    cursor: pointer;
}
